.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

header, footer  {
  text-align: left; /* Align text to the left */
  margin-bottom: 30px;
  padding: 40px; /* Increased padding for a larger header box */
  background-color: #0081A2; /* Updated blue background color */
  color: white; /* White text */
  border-radius: 5px; /* Optional: To round the corners of the box */
}

header h1 {
  margin: 0;
  font-size: 24px;
}

header p {
  margin: 10px 0 0 0;
  font-size: 14px;
}

footer {
  margin-top: auto;
  background-color: #0081A2;
  color: white;
  text-align: center;
  padding: 1px;
}


.define-household {
  margin-bottom: 30px;
}

.filters {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.filter-item {
  flex: 1;
  min-width: 200px;
  display: flex;
  flex-direction: column; /* Stack label and dropdown vertically */
}

.filter-item label {
  margin-bottom: 8px; /* Space between label and dropdown */
}

.apply-button {
  padding: 10px 20px;
  background-color: #007bff; /* Regular blue background color for buttons */
  color: #fff;
  border: none;
  cursor: pointer;
  align-self: center;
  margin-top: 20px; /* Add margin to separate from input fields */
}

.apply-button:hover {
  background-color: #0056b3; /* Darker shade of the regular blue for hover effect */
}

.income-scale {
  margin-bottom: 30px;
}

.scale {
  position: relative;
  height: 50px;
  margin-top: 20px;
}

.line {
  height: 10px;
  background: linear-gradient(to right, red, yellow, green);
  position: relative;
  cursor: pointer;
}

.line span {
  position: absolute;
  bottom: -20px;
  font-size: 12px;
}

.line .low {
  left: 0;
}

.line .mid {
  left: 25%;
}

.line .high {
  right: 0;
}

.indicator {
  position: absolute;
  top: 20px; /* Positioned below the line */
  background-color: #fff;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 12px;
  transform: translateX(-50%); /* Center the indicator at the calculated position */
  white-space: nowrap; /* Prevent the text from wrapping to a new line */
}

.indicator::before {
  content: '';
  position: absolute;
  bottom: 100%; /* Start the line from the bottom of the box */
  left: 50%; /* Center the line horizontally */
  width: 2px; /* Line width */
  height: 11px; /* Adjust height to connect the box to the income scale */
  background-color: black; /* Line color */
  transform: translateX(-50%);
}

.indicator2 {
  position: absolute;
  top: 60px; /* Positioned below the line */
  background-color: #fff;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 12px;
  transform: translateX(-50%); /* Center the indicator at the calculated position */
  white-space: nowrap; /* Prevent the text from wrapping to a new line */
}

.indicator2::before {
  content: '';
  position: absolute;
  bottom: 100%; /* Start the line from the bottom of the box */
  left: 50%; /* Center the line horizontally */
  width: 2px; /* Line width */
  height: 51px; /* Adjust height to connect the box to the income scale */
  background-color: black; /* Line color */
  transform: translateX(-50%);
}

.income-indicator {
  background-color: #fff;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 12px;
  transform: translateX(-50%); /* Center the indicator */
  top: -50px; /* Adjusts the position 10px higher than the other indicators */
  position: absolute;
  transform: translateX(-50%);
  white-space: nowrap; /* Prevent the text from wrapping to a new line */
}

.income-indicator::before {
  content: '';
  position: absolute;
  top: 100%; /* Start the line from the bottom of the box */
  left: 50%; /* Center the line horizontally */
  width: 2px; /* Line width */
  height: 12px; /* Adjust height to connect the box to the income scale */
  background-color: black; /* Line color */
  transform: translateX(-50%);
}

.spending-pattern {
  margin-bottom: 30px;
}

.charts {
  display: flex;
  justify-content: space-between;
}

.chart {
  text-align: center;
  flex: 1;
  max-width: 40%;
}

.pie-chart {
  width: 100px;
  height: 100px;
  background-color: #ccc;
  border-radius: 50%;
  margin: 0 auto;
}

.policy-profile {
  margin-bottom: 30px;
}

.policy-profile p {
  line-height: 1.6;
}




@keyframes zoomIn {
  from {
    transform: translate(-50%, -50%) scale(0);
    opacity: 0;
  }
  to {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
}

.toggle-button {
  background: none;
  cursor: pointer;
  border: inset;
  margin-right: 10px;
  font-size: 12px;
  min-width: 25px;
}

.definition-section {
  align-items: center;
  margin: 0;
  font-size: 12px;
}


@keyframes zoomOut {
  from {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
  to {
    transform: translate(-50%, -50%) scale(0);
    opacity: 0;
  }
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: 1px solid #ccc;
  padding: 20px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.5); /* Thick drop shadow */
  z-index: 1000;
  width: 300px;
  text-align: center;
  animation: zoomIn 0.3s ease-in-out; /* Zoom-in animation */
}

.popup.zoom-out {
  animation: zoomOut 0.3s ease-in-out forwards; /* Zoom-out animation */
}

.popup-content p {
  margin-bottom: 20px;
}

.popup-content button {
  padding: 10px 20px;
  background-color: #0081A2;
  color: white;
  border: none;
  cursor: pointer;
}

.popup-content button:hover {
  background-color: #005f73;
}
